:root {
  font-size: 62.5%;
}

*, *::before, ::after {
  box-sizing: border-box;
}

* {
  padding: 0;
  margin: 0;
  border: 0;
}

main {
  display: flex;
  flex-flow: column;
  max-width: 85%;
  margin: auto;
  text-align: center;
}

.card {
  display: grid;
  margin: 2em 0;
  padding: 2em;
  text-align: left;
  max-height: 40%;
}

.card-icon {
  justify-self: right;
  width: 4.35em;
  margin-top: 3em;
}

footer {
  text-align: center;
  padding: 1em;
}

@media (min-width: 60rem) {
  main {
    place-self: center;
    margin: 3em auto;
  }

  .card-container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 1.5em;
  }

  .mid-section {
    display: flex;
    flex-flow: column;
  }

  .card-cyan, .card-blue {
    align-self: center;
  }

  .title {
    max-width: 50%;
    margin: auto;
  }
  .title p {
    margin: 2em 0;
  }
}
:root {
  --Red: hsl(0, 78%, 62%);
  --Cyan: hsl(180, 62%, 55%);
  --Orange: hsl(34, 97%, 64%);
  --Blue: hsl(212, 86%, 64%);
  --Dark-Blue: hsl(234, 12%, 34%);
  --Gray-Blue: hsl(229, 6%, 66%);
  --Light-Gray: hsl(0, 0%, 98%);
  --Main-font-family: "Poppins", sans-serif;
}

* {
  font-family: var(--Main-font-family);
  font-size: 1.5rem;
}

body {
  background: var(--Light-Gray);
}

h1 {
  color: var(--Dark-Blue);
  font-weight: 700;
  font-size: 2.4rem;
}

p {
  font-weight: 400;
  color: var(--Gray-Blue);
  line-height: 1.7em;
}

.title h1 {
  font-size: 3.2rem;
}
.title .secondary-title {
  font-weight: 200;
}

.card {
  background: white;
  box-shadow: 0em 1.5em 1.5em -1em var(--Gray-Blue);
  border-radius: 0.5em;
}
.card p {
  font-size: 1.3rem;
}

.card-red {
  border-top: solid var(--Red) 0.3em;
}

.card-orange {
  border-top: solid var(--Orange) 0.3em;
}

.card-cyan {
  border-top: solid var(--Cyan) 0.3em;
}

.card-blue {
  border-top: solid var(--Blue) 0.3em;
}

footer p {
  color: #292727;
}
footer a {
  text-decoration: none;
}